@use '/src/styles/constants/colors';

.accountWrapper {
  padding: 20px;
  background-color: colors.$SECONDARY;
}

.buttonWrapper {
  display: flex;
  gap: 16px;
  padding-top: 20px;
}

.accountItem {
  display: flex;
  align-items: center;
  justify-content: space-between;

  & + & {
    margin-top: 16px;
  }

  .accountList {
    text-align: start;

    .name {
      line-height: 1.5;

      span {
        font-weight: 700;
      }
    }

    .account {
      color: colors.$GRAY3;
    }
  }

  .accountMethod {
    display: flex;
    align-items: center;
    gap: 10px;

    .copyButton {
      width: auto;
    }

    .disabled {
      pointer-events: none;
      cursor: not-allowed;
    }

    .disabledKaKaoPay {
      filter: grayscale(100%);
    }
  }

  svg {
    width: 30px;
    height: 30px;
    margin: 10px;
    fill: colors.$PRIMARY;
  }
}