@use '/src/styles/constants/colors';

.welcome {
  p {
    margin: 20px 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 34px;
    color: colors.$GRAY1;
    text-align: center;
  }

  .host {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 25px 0;
    font-size: 14px;
    background-color: colors.$SECONDARY;

    & > div {
      display: flex;
      justify-content: space-between;
      width: 200px;

      + div {
        margin-top: 10px;
      }
    }

    span {
      font-size: 18px;
      font-weight: 700;
      color: colors.$GRAY1;
    }

    .buttonWrapper {
      margin-top: 25px;

      button {
        background-image: url('../../../assets/icons/telephone.svg');
        background-size: 20px;
        background-repeat: no-repeat;
        background-position: 10% center;
        filter: invert(88%) sepia(30%) saturate(3005%) hue-rotate(287deg) brightness(102%) contrast(85%);
        color: initial;
        border-color: initial;
      }
      .phone {
        fill: colors.$PRIMARY;
      }
    }
  }
}

.connect {
  display: flex;
  flex-direction: column;
  gap: 20px;

  div:nth-child(3) {
    border-bottom: 1px solid colors.$GRAYE;
    padding-bottom: 20px;
  }

  .connectItem {
    display: flex;
    align-items: center;

    span:nth-child(1), span:nth-child(2) {
      width: 30%;
    }

    span:nth-child(3) {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 15px;
      width: 40%;

      a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        background-color: colors.$PRIMARY_THICK;
        border-radius: 50%;

        svg {
          fill: colors.$WHITE;
        }
      }

      a:nth-child(1) {
        svg {
          width: 20px;
          height: 20px;
        }
      }

      a:nth-child(2) {
        svg {
          width: 30px;
          height: 30px;
        }
      }
    }
  }
}

